import 'swiper/css';
import Swiper, { Navigation } from 'swiper';
import 'swiper/css/pagination';

const prev = document.querySelector('.reviews__nav-left');
prev.classList.toggle('reviews__left-gray');

Swiper.use([Navigation]);

const Slider = function(){
  const swiper = new Swiper('.swiper', {

    loop: false,
    slidesPerView: 1,
    
    navigation: {
      nextEl: '.reviews__nav-right',
      prevEl: '.reviews__nav-left',
    },
    on: {
      slideChange: function () {
        if(this.activeIndex == 2)
        {
          const next = document.querySelectorAll('.reviews__nav-right');
          next.forEach(element => {
            element.classList.toggle('reviews__right-gray');
          });
        }
        else
        {
          const next = document.querySelectorAll('.reviews__right-gray');
          next.forEach(element => {
            element.classList.remove('reviews__right-gray');
            element.classList.add('reviews__nav-right');
          });
        }
        if(this.activeIndex == 0)
        {
          const prev = document.querySelectorAll('.reviews__nav-left');
          prev.forEach(element => {
            element.classList.toggle('reviews__left-gray');
          });
        }
        else
        {
          const prev = document.querySelectorAll('.reviews__left-gray');
          prev.forEach(element => {
            element.classList.remove('reviews__left-gray');
            element.classList.add('reviews__nav-left');
          });
        }
      },
    }
  });
};

export default Slider();
